.products-card-container{display:grid;gap:20px}@media screen and (min-width: 1920px){.products-card-container{grid-template-columns:repeat(5, 1fr)}}@media(min-width: 1366px)and (max-width: 1920px){.products-card-container{grid-template-columns:repeat(4, 1fr)}}@media(min-width: 768px)and (max-width: 1366px){.products-card-container{grid-template-columns:repeat(3, 1fr)}}@media(min-width: 480px)and (max-width: 768px){.products-card-container{grid-template-columns:repeat(2, 1fr)}}@media(max-width: 480px){.products-card-container{grid-template-columns:1fr}}